<template>
  <div class="recommend-item" @click="toDetail(item)">
    <div class="img-box">
      <img :src="item.imgSrc"/>
    </div>
    <div class="recommend-box">
      <div class="recommend-title">
        <el-tooltip effect="light" :content="item.title" :disabled="isTitleShort" placement="left">
          <span>{{item.title|formatTitle}}</span>
        </el-tooltip>
      </div>
      <div class="recommend-sub">
        <div>
          <i class="iconfont icon-jiaoshi"></i>
          主讲教师：{{item.teacher}}
        </div>
        <div>
          <i class="iconfont icon-shizhong3"></i>
          开讲时间：{{item.date}}&nbsp;{{item.time}}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { getLengthInEN } from "../../utils/utils.js";
import { fitStr } from "../../utils/utils.js";
export default {
  props: {
    item: {
      type: Object
    },
    isLive: {
      type: Boolean,
      default: false
    }
  },
  filters: {
    formatTitle(str) {
      if (str) {
        return fitStr(str, 18);
      } else {
        return "未知";
      }
    }
  },
  data() {
    return {
      isClsShort: true,
      isTitleShort: true
    };
  },
  created() {
    if (getLengthInEN(this.item.class) > 25) {
      this.isClsShort = false;
    }
    if (getLengthInEN(this.item.title) > 18) {
      this.isTitleShort = false;
    }
  },
  methods: {
    toDetail(item) {
      if (this.isLive) {
        this.$router.push({
          name: "CourseDetail",
          query: { live: this.isLive, ClassroomID: item.clsid, recid: item.id }
        });
      } else {
        this.$router.push({
          name: "CourseDetail",
          query: { MaterialId: item.id, recid: item.recid }
        });
      }
    }
  }
};
</script>
<style lang="less" scoped>
.recommend-item {
  width: 220px;
  height: 215px;
  text-align: left;
  cursor: pointer;
  .img-box {
    width: 100%;
    height: 120px;
    border-radius: 12px;
    overflow: hidden;
  }
  .recommend-box {
    div {
      width: 100%;
    }
    .recommend-title {
      margin: 8px auto;
    }
    .recommend-sub div {
      font-size: 0.9rem;
      color: #888888;
      .iconfont {
        font-size: 0.8rem;
        color: #317fff;
      }
    }
  }
}
</style>



