<template>
  <div class="in-table">
    <div class="inc-table-head">
      <el-date-picker
        class="fl"
        v-model="dateRange"
        type="daterange"
        align="right"
        unlink-panels
        size="small"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        :picker-options="pickerOptions"
      ></el-date-picker>
      <el-dropdown class="fr">
        <el-button type="primary" size="small">
          批量操作
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>批量删除</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-input placeholder="请输入内容" size="small" v-model="searchInput" class="fr search-input">
        <el-button size="small" slot="append" icon="el-icon-search"></el-button>
      </el-input>
      <div class="clear"></div>
    </div>
    <div class="inc-table-week">
      <div class="week-icon fl">
        <i class="iconfont icon-shizhong2"></i>
      </div>
      <div class="week-choose fr">
        <span
          class="week-item"
          v-for="week in weeks"
          :key="week.text"
          :class="{'active-bgwt': week.choosed}"
          @click="chooseWeek(week)">
          <span class="number">{{week.num}}</span>
          <span class="text">{{week.text}}</span>
        </span>
      </div>
      <i class="clear"></i>
    </div>
    <common-table
      class="inc-table"
      th-class="inc-table-innerhead"
      :columns="tableColumns"
      :datas="tableData"
      :pagger="false"
    ></common-table>
  </div>
</template>
<script>
import commonTable from "../commonTable.vue";
export default {
  components: {
    commonTable
  },
  data() {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      dateRange: null,
      searchInput: "",
      weeks: [
        {
          choosed: false,
          num: 125,
          text: "周一"
        },
        {
          choosed: false,
          num: 125,
          text: "周二"
        },
        {
          choosed: true,
          num: 77,
          text: "周三"
        },
        {
          choosed: false,
          num: 125,
          text: "周四"
        },
        {
          choosed: false,
          num: 100,
          text: "周五"
        },
        {
          choosed: false,
          num: 125,
          text: "周六"
        },
        {
          choosed: false,
          num: 85,
          text: "周日"
        }
      ],
      tableColumns: [
        {
          prop: "grade",
          label: "年级"
        },
        {
          prop: "subject",
          label: "学科"
        },
        {
          prop: "title",
          label: "标题"
        },
        {
          prop: "school",
          label: "学校"
        },
        {
          prop: "teacher",
          label: "讲师"
        },
        {
          prop: "time",
          label: "时段"
        }
      ],
      tableData: [
        {
          grade: "大一",
          subject: "临床医学",
          title: "全解析人体解剖学基础理论知识",
          school: "福建医科大学",
          teacher: "李飞鹏",
          time: "08:00 ~ 08:45"
        },
        {
          grade: "大二",
          subject: "高等数学",
          title: "深度学习<线性代数>和<概率论>",
          school: "福州大学",
          teacher: "曾珊妮",
          time: "14:00 ~ 16:00"
        },
        {
          grade: "大四",
          subject: "计算机科学与技术",
          title: "密码学应用或网络游戏理论",
          school: "福建师范大学",
          teacher: "郑志民",
          time: "09:50 ~ 10:35"
        },
        {
          grade: "大三",
          subject: "外国语言文学",
          title: "高级英语完形填空学习技巧及能力提升",
          school: "莆田学院",
          teacher: "吴润珍",
          time: "14:55 ~ 15:40"
        },
        {
          grade: "大二",
          subject: "人工智能",
          title: "深度学习算法全解析（期末冲刺）",
          school: "华侨大学",
          teacher: "李金铭",
          time: "09:50 ~ 10:35"
        }
      ]
    };
  },
  methods: {
    chooseWeek(ele) {
      this.weeks.forEach(element => {
        if (ele === element) {
          this.$set(element, "choosed", true);
        } else {
          this.$set(element, "choosed", false);
        }
      });
    }
  }
};
</script>
<style lang="less">
.in-table {
  width: 100%;
  .inc-table-head {
    width: 100%;
    .search-input {
      width: 200px;
      margin-right: 25px;
    }
  }
  .inc-table-week {
    width: 100%;
    height: 50px;
    margin-top: 15px;
    color: #888;
    div {
      height: 100%;
      background: #f3f7fa;
    }
    .week-icon {
      width: 50px;
      line-height: 50px;
      cursor: pointer;
      .iconfont {
        font-size: 1.5rem;
      }
    }
    .week-choose {
      width: 1125px;
      display: flex;
      justify-content: space-between;
      span {
        display: block;
      }
      .week-item {
        width: 14.28%;
        cursor: pointer;
        .number {
          color: #45aff9;
          font-size: 1.25rem;
        }
        .text {
          font-size: 0.6rem;
        }
      }
      .active-bgwt {
        background: #45aff9;
        color: #fff;
        .number {
          color: #fff;
        }
      }
    }
  }
  // 表格
  .inc-table {
    width: 100%;
    margin-top: 15px;
    .inc-table-innerhead th {
      background: #dceaf5;
    }
  }
}
</style>



