<template>
  <div class="master-item">
    <div class="bg"></div>
    <div class="context">
      <div class="avater">
        <img class="img" :src="item.avater" :draggable="false">
      </div>
      <div class="name">{{item.name}}</div>
      <div class="call-name">
          <span class="sub">{{item.subject}}</span>
          <span class="sub">{{item.level}}</span>
      </div>
      <div class="detail">{{item.detail}}</div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    item: Object
  }
};
</script>
<style lang="less" scoped>
.master-item {
  width: 215px;
  height: 288px;
  position: relative;
  font-size: 0.88rem;
  overflow: hidden;
  .bg {
    width: 100%;
    height: 245px;
    position: absolute;
    bottom: 0;
    background: #F3F7FA;
    border-radius: 8px;
  }
  .context {
    width: 100%;
    height: 100%;
    position: absolute;
    .avater {
      width: 85px;
      height: 85px;
      margin: 0 auto;
      border-radius: 50%;
      overflow: hidden;
      .img {
        width: 100%;
        height: 100%;
      }
    }
    .name {
      margin: 0 auto;
      margin-top: 8px;
    }
    .call-name {
      width: 100%;
      margin-top: 8px;
      .sub {
        padding: 0 15.2px;
        color: #888888;
      }
    }
    .detail {
      padding: 10px 25px;
      font-size: 0.8rem;
      color: #999999;
      text-align: left;
    }
  }
}
</style>



