<template>
  <div class="in-container">
    <div class="inc-heade">
      <i class="iconfont icon-sijiaoxing"></i>
      <i class="iconfont icon-shuji1"></i>
      <span
        class="title"
      >{{titleArr[0]}}&nbsp;/&nbsp;{{titleArr[1]}}&nbsp;/&nbsp;{{titleArr[2]}}&nbsp;/&nbsp;{{titleArr[3]}}</span>
      <i class="iconfont icon-shuji1"></i>
      <i class="iconfont icon-sijiaoxing"></i>
    </div>
    <div class="inc-box">
      <slot name="box-item"></slot>
    </div>
    <slot name="box"></slot>
  </div>
</template>
<script>
export default {
  props: {
    title: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      titleArr: []
    };
  },
  created() {
    this.setTitle();
  },
  methods: {
    setTitle() {
      this.titleArr = [...this.title.split("")];
    }
  }
};
</script>
<style lang="less" scoped>
.in-container {
  margin-bottom: 28px;
  .inc-heade {
    margin: 0 auto;
    font-weight: 500;
    font-size: 1.1rem;
    color: #000;
    .iconfont {
      font-size: 1.25rem;
      color: #47a0ff;
    }
    .icon-sijiaoxing::before {
      transform: rotate(45deg);
      display: inline-block;
    }
  }
  .inc-box {
    width: 100%;
    margin-top: 15px;
    display: flex;
    justify-content: space-between;
  }
}
</style>



